import {Injectable} from "@angular/core";
import {ToastController, ModalController, Platform} from "ionic-angular";

import {Constants} from "../../entities/Constants";
import {SpinnerComponent} from "../../components/spinner/spinner";
import {ProcessResult} from "../../entities/ProcessResult";


@Injectable()
export class UtilProvider {

    spinner: any;
    isSpinnerRunning: boolean = false;

    constructor(private toast: ToastController,
                private platform: Platform,
                private modalController: ModalController) {

    }


    platformIs(platformName: string): boolean {
        return this.platform.is(platformName);
    }

    assign(item: any): any {
        if (this.isEmpty(item)) return null;

        return Object.assign({}, item);
    }

    assignList(list: any[]): any[] {
        let newList: any[] = [];
        list.forEach(item => {
            newList.push(this.assign(item));
        });

        return newList;

    }


    ifOffline() {
        this.error("Bu işlemi yapabilmek için internet bağlantısı gereklidir.");
        this.loaderEnd();
    }


    timerStart(name: string) {
        console.time(name);
    }

    timerEnd(name: string) {
        console.timeEnd(name);
    }

    loaderStart(showPercentage: boolean = false) {
        if (!this.isSpinnerRunning) {
            this.spinner = this.modalController.create(SpinnerComponent,
                {showPercentage: showPercentage},
                {enableBackdropDismiss: false, cssClass: this.getSelectedTheme()});
            this.spinner.present();
        }
        this.isSpinnerRunning = true;
    }

    loaderEnd() {
        if (this.isSpinnerRunning)
            this.spinner.dismiss();
        this.isSpinnerRunning = false;
    }


    /* Message Utils  */


    getDuration() {

        return 2 * 1000;
    }

    success(message: string) {
        let toast = this.toast.create({
            message: message,
            duration: this.getDuration(),
            position: "top",
            cssClass: "toast-success"
        });
        toast.present();

        // this.addMessage('success', 'Başarılı', message);
    }

    info(message: string) {
        let toast = this.toast.create({
            message: message,
            duration: this.getDuration(),
            position: "top",
            cssClass: "toast-info"
        });
        toast.present();
        //this.addMessage('info', 'Bilgi', message);
    }

    warn(message: string) {
        let toast = this.toast.create({
            message: message,
            duration: this.getDuration(),
            position: "top",
            cssClass: "toast-warn"
        });
        toast.present();
        //this.addMessage('warn', 'Uyarı', message);
    }


    error(message: string) {
        let toast = this.toast.create({
            message: message,
            duration: this.getDuration(),
            position: "top",
            cssClass: "toast-error"
        });
        toast.present();

        // this.messageService.add({severity: 'info', summary: 'Uyarı', detail: message});
        //this.addMessage('info', 'Uyarı', message);
    }

    getSystemParam(param) {
        return this.getLocalStorageParam("systemParams", param);
    }

    getSystemLabel(param) {
        return this.getLocalStorageParam("labels", param);
    }

    private getLocalStorageParam(type: string, param: string) {
        let value: string = "";
        let systemParams = localStorage.getItem(type);
        if (systemParams != null) {
            let paramList = JSON.parse(systemParams);
            paramList.forEach(item => {
                if (item.kod != null && item.kod == param) {
                    value = item.ad;
                }
            });
        }
        return value;
    }

    /* MESSAGE UTIL END */


    /* DATE UTILS START   */


    dateFormatRegex(x, y = "dd-MM-yyyy"): string {
        if (this.isEmpty(x)) return "";

        x = new Date(x);
        let z = {
            M: x.getMonth() + 1,
            d: x.getDate(),
            h: x.getHours(),
            m: x.getMinutes(),
            s: x.getSeconds()
        };
        y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
            return ((v.length > 1 ? "0" : "") + eval("z." + v.slice(-1))).slice(-2);
        });

        return y.replace(/(y+)/g, function (v) {
            return x
                .getFullYear()
                .toString()
                .slice(-v.length);
        });
    }


    newDateTime(dateString: any): number {

        if (this.isNotEmpty(dateString) && typeof dateString == "number")
            return dateString;

        if (this.isNotEmpty(dateString)) {

            let index = dateString.indexOf('.');

            if (index > -1)
                dateString = dateString.substr(0, index).replace(/-/g, "/");
            else
                dateString = dateString.replace(/-/g, "/");

            return new Date(dateString).getTime();

        }

        return null;
    }

    addMinutes(dateStr: Date, addition: number): Date {
        return new Date(dateStr.setTime(dateStr.getTime() + 1000 * 60 * addition));
    }

    addDays(dateTime: number, addition: number = 1): number {
        return dateTime + 24 * 60 * 60 * 1000 * addition;
    }

    addDay(dateTime) {
        return this.addDays(dateTime, 1);
    }


    getLocaleParameters(locale?: any): any {
        switch (locale) {
            case "TR": {
                return this.getTurkishLocale();
            }

            default: {
                return this.getTurkishLocale();
            }
        }
    }

    private getTurkishLocale(): any {
        return {
            firstDayOfWeek: 1,
            dayNames: ["Pazar", "Pazartesi", "Salı", "Çarşamba", "Perşembe", "Cuma", "Cumartesi"],
            dayNamesShort: ["Pz", "Pt", "Sa", "Ça", "Pe", "Cu", "Ct"], //["Paz", "Pzt", "Sal", "Çar", "Per", "Cum", "Cmt"],
            dayNamesMin: ["Pz", "Pt", "Sa", "Ça", "Pe", "Cu", "Ct"],
            monthNames: ["Ocak", "Şubat", "Mart", "Nisan", "Mayıs", "Haziran", "Temmuz", "Ağustos", "Eylül", "Ekim", "Kasım", "Aralık"],
            monthNamesShort: ["Oca", "Şub", "Mar", "Nis", "May", "Haz", "Tem", "Ağu", "Eyl", "Eki", "Kas", "Ara"],
            today: 'Bugün',
            clear: 'Temizle'
        };

    }

    /* DATE UTIL END   */


    /* STRING UTIL START*/

    translateTurkishCharacters(text: string) {
        let z = "";
        if (text != null && text != "") {
            let y = text.split("");
            for (let i = 0; y.length > i; i++) {
                switch (y[i]) {
                    case "Ç":
                        z += y[i].replace("Ç", "C");
                        break;
                    case "ç":
                        z += y[i].replace("ç", "c");
                        break;
                    case "Ğ":
                        z += y[i].replace("Ğ", "G");
                        break;
                    case "ğ":
                        z += y[i].replace("ğ", "g");
                        break;
                    case "İ":
                        z += y[i].replace("İ", "I");
                        break;
                    case "ı":
                        z += y[i].replace("ı", "i");
                        break;
                    case "Ö":
                        z += y[i].replace("Ö", "O");
                        break;
                    case "ö":
                        z += y[i].replace("ö", "o");

                        break;
                    case "Ş":
                        z += y[i].replace("Ş", "S");
                        break;
                    case "ş":
                        z += y[i].replace("ş", "s");
                        break;
                    case "Ü":
                        z += y[i].replace("Ü", "U");
                        break;
                    case "ü":
                        z += y[i].replace("ü", "u");
                        break;
                    default:
                        z += y[i];
                        break;
                }
            }
        }

        return z;
    }


    /* STRING UTIL END*/


    isGreaterThan(mst: number, compared: number): boolean {
        let result = false;

        if (this.isNotEmpty(mst) && this.isNotEmpty(compared)) {
            return mst > compared;
        }
        return result;
    }

    isGreaterOrEqualThan(mst: number, compared: number): boolean {
        let result = false;

        if (this.isNotEmpty(mst) && this.isNotEmpty(compared)) {
            return mst >= compared;
        }
        return result;
    }

    isLessThan(mst: number, compared: number): boolean {
        let result = false;

        if (this.isNotEmpty(mst) && this.isNotEmpty(compared)) {
            return mst < compared;
        }
        return result;
    }

    isLessOrEqualThan(mst: number, compared: number): boolean {
        let result = false;

        if (this.isNotEmpty(mst) && this.isNotEmpty(compared)) {
            return mst <= compared;
        }
        return result;
    }


    isEmpty(item: any): boolean {
        if (item != null)
            item = String(item);

        return item == "undefined" || typeof item == "undefined" || item == null || item == "";
    }

    emptyIfNull(item: any) {
        if (this.isEmpty(item)) item = "";

        return item;
    }

    isNotEmpty(item: any): boolean {
        return !this.isEmpty(item);
    }

    isNotEmptyRows(res): boolean {
        return (this.isNotEmpty(res) && this.isNotEmpty(res.rows) && res.rows.length > 0);
    }


    getSelectedTheme(): string {
        return "blue-theme";
    }

    getBackgroundImage() {
        return "assets/images/login-background.jpg";
    }


    mailRegex(): RegExp {
        return new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);
    }

    passwordRegex(): RegExp {
        return new RegExp("^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=\\S+$).{6,}$");
    }

    processResult(result: ProcessResult) {
        result.errorMessages.forEach(mes => {
            this.error(mes);
        });

        result.infoMessages.forEach(mes => {
            this.info(mes);
        });

        return result.objects;

    }

}



