import {Component} from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams} from 'ionic-angular';
import {TodoList} from "../../entities/TodoList";
import {TodoListEndpoint} from "../../providers/todo-list-endpoint/todo-list-endpoint";
import {TodoListEditComponent} from "../../components/todo-list-edit/todo-list-edit";
import {UtilProvider} from "../../providers/util/util";
import {TodoItemPage} from "../todo-item/todo-item";
import {Observable} from "rxjs/Observable";

/**
 * @author mali.sahin
 * @since 15.12.2018
 */

@IonicPage()
@Component({
    selector: 'page-todo-list',
    templateUrl: 'todo-list.html',
})
export class TodoListPage {


    list: TodoList[] = [];

    constructor(public navCtrl: NavController,
                private todoListEndpoint: TodoListEndpoint,
                private modalController: ModalController,
                private util: UtilProvider,
                public navParams: NavParams) {

        this.getList();

    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad TodoListPage');
    }

    getList() {
        let call = this.todoListEndpoint.list();

        call.subscribe(
            (res) => {
                this.list = this.util.processResult(res);
                console.log(res);
            },
            (err) => {
                this.util.processResult(err.error);
                console.error(err);
            },
            () => {
                console.info("Completed")
            }
        );
    }

    goToDetail(todoList: TodoList) {
        this.navCtrl.push(TodoItemPage, {todoList: todoList});
        console.log(todoList);
    }

    createNewOne() {
        let modal = this.modalController
            .create(TodoListEditComponent,
                {},
                {
                    cssClass: this.util.getSelectedTheme()
                });

        modal.onDidDismiss(() => {
            this.getList();
        });
        modal.present();
    }

    delete(todoList: TodoList) {
        let call = this.todoListEndpoint.delete(todoList.id);
        this.onComplete(call);
    }

    onComplete(call: Observable<any>) {
        call.subscribe(
            () => {
                this.getList();
            },
            (err) => {
                this.util.processResult(err.error);
                console.error(err);
            },
            () => {
                console.info("Completed")
            }
        );
    }

}



