import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable} from "rxjs/Observable";
import {ApiProvider} from "../api/api";
import {TodoList} from "../../entities/TodoList";
import {TodoItem} from "../../entities/TodoItem";

/**
 * @author mali.sahin
 * @since 13.12.2018
 */
@Injectable()
export class TodoItemEndpoint {


    header: HttpHeaders;
    url: string;

    constructor(private api: ApiProvider, private http: HttpClient) {

        this.header = this.api.getHeader();
    }

    save(todoItem: TodoItem): Observable<any> {
        this.url = this.api.saveItemUrl();
        return this.http.post(this.url, todoItem, {headers: this.header});
    }

    list(listId: number): Observable<any> {
        this.url = this.api.findItemUrl(listId);
        return this.http.get(this.url,
            {headers: this.header});
    }

    delete(itemId): Observable<any> {
        this.url = this.api.deleteItemUrl(itemId);
        return this.http.delete(this.url,
            {headers: this.header});
    }

    update(todoItem: TodoItem): Observable<any> {
        this.url = this.api.updateItemUrl();
        return this.http.put(this.url, todoItem, {headers: this.header});
    }

}



