import {Component} from '@angular/core';
import {TodoList} from "../../entities/TodoList";
import {ViewController} from "ionic-angular";
import {TodoListEndpoint} from "../../providers/todo-list-endpoint/todo-list-endpoint";
import {UtilProvider} from "../../providers/util/util";

/**
 * @author mali.sahin
 * @since 15.12.2018
 */
@Component({
    selector: 'todo-list-edit',
    templateUrl: 'todo-list-edit.html'
})
export class TodoListEditComponent {

    text: string;
    todoList: TodoList;

    constructor(private viewController: ViewController,
                private util: UtilProvider,
                private todoListEndpoint: TodoListEndpoint) {

        this.todoList = new TodoList();
    }

    validate() {
        if (this.util.isNotEmpty(this.todoList.name))
            this.save();
        else
            this.util.error("Alias can not be null");
    }

    save() {
        let call = this.todoListEndpoint.save(this.todoList);

        call.subscribe(
            (res) => {
                console.log(res);
                let objects = this.util.processResult(res);

                if (objects.length > 0)
                    this.returnListPage();

            },
            (err) => {
                console.log(err);
                this.util.processResult(err.error);
            },
            () => {
                console.log("Completed");
            }
        )
    }

    returnListPage() {
        this.util.success("Todo List is saved.");
        this.closeModal();

    }


    closeModal() {
        this.viewController.dismiss();
    }

}



