import {BrowserModule, DomSanitizer} from "@angular/platform-browser";
import {NgModule, ErrorHandler, CUSTOM_ELEMENTS_SCHEMA} from "@angular/core";
import {IonicApp, IonicModule, IonicErrorHandler, ToastController} from "ionic-angular";
import {MyApp} from "./app.component";
import {StatusBar} from "@ionic-native/status-bar";
import {SplashScreen} from "@ionic-native/splash-screen";

import {LoginPage} from "../pages/login/login";
import {LoginProvider} from "../providers/login/login";
import {ApiProvider} from "../providers/api/api";
import {HttpClientModule} from "@angular/common/http";
import {UtilProvider} from "../providers/util/util";
import {Toast} from "@ionic-native/toast";
import {LoggerProvider} from "../providers/logger/logger";
import {Network} from "@ionic-native/network";
import {Anasayfa} from "../pages/anasayfa/anasayfa";
import {AnasayfaPageModule} from "../pages/anasayfa/anasayfa.module";
import {HeaderProvider} from "../providers/header/header";
import {LoginPageModule} from "../pages/login/login.module";

import 'intl';
import 'intl/locale-data/jsonp/en';
import {PipesModule} from "../pipes/pipes.module";
import {CacheModule} from "ionic-cache";

import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import {DateFormatPipe} from "../pipes/date-format/date-format";
import {ComponentsModule} from "../components/components.module";
import {InAppBrowser} from "@ionic-native/in-app-browser";
import {UserEndpoint} from '../providers/user-endpoint/user-endpoint';
import {TodoListEndpoint} from '../providers/todo-list-endpoint/todo-list-endpoint';
import {TodoItemEndpoint} from '../providers/todo-item-endpoint/todo-item-endpoint';
import {RegisterPageModule} from "../pages/register/register.module";
import {UserService} from '../providers/user-service/user-service';
import {TodoListPageModule} from "../pages/todo-list/todo-list.module";
import {TodoItemPageModule} from "../pages/todo-item/todo-item.module";
import {TodoListPage} from "../pages/todo-list/todo-list";
import {TodoItemPage} from "../pages/todo-item/todo-item";


@NgModule({
    declarations: [
        MyApp
    ],
    imports: [
        ComponentsModule,
        BrowserModule,
        BrowserAnimationsModule,
        PipesModule,
        IonicModule.forRoot(MyApp),
        CacheModule.forRoot(),
        HttpClientModule,
        AnasayfaPageModule,
        LoginPageModule,
        BrowserAnimationsModule,
        PipesModule,
        RegisterPageModule,
        TodoListPageModule,
        TodoItemPageModule
    ],
    exports: [],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        LoginPage,
        Anasayfa,
        TodoListPage,
        TodoItemPage

    ],
    providers: [
        Network,
        StatusBar,
        SplashScreen,
        {provide: ErrorHandler, useClass: IonicErrorHandler},
        LoginProvider,

        ApiProvider,
        UtilProvider,
        Toast,
        ToastController,
        LoggerProvider,
        HeaderProvider,
        DateFormatPipe,
        InAppBrowser,
        UserEndpoint,
        TodoListEndpoint,
        TodoItemEndpoint,
        UserService,
    ],
    schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppModule {
    constructor() {
    }
}



