import {Component} from "@angular/core";
import {NavController, AlertController, LoadingController, Loading, IonicPage} from "ionic-angular";
import {LoginProvider} from "../../providers/login/login";
import {UtilProvider} from "../../providers/util/util";
import {LoggerProvider} from "../../providers/logger/logger";
import {User} from "../../entities/User";
import {Anasayfa} from "../anasayfa/anasayfa";
import {Constants} from "../../entities/Constants";
import {Observable} from "rxjs";
import {RegisterPage} from "../register/register";
import {UserService} from "../../providers/user-service/user-service";
import {TodoListPage} from "../todo-list/todo-list";

class UserInfo {
    userCode: string = "";
    password: string = "";
}

@IonicPage()
@Component({
    selector: "page-login",
    templateUrl: "login.html"
})
export class LoginPage {
    passwordType: string = "password";
    passwordIcon: string = "eye-off";
    loading: Loading;

    user: User;

    showPassword: boolean = false;
    spinner: any;
    isUserValid: boolean;

    constructor(private nav: NavController,
                private util: UtilProvider,
                private logger: LoggerProvider,
                private userService: UserService,
                private loginProvider: LoginProvider) {

        this.user = new User();
        this.user.email = 'ali@ali.com';
        this.user.password = 'Shn123'

    }


    ionViewDidLoad() {
        this.logger.info("Login Page ionViewDidLoad");
    }

    login() {
        this.validateUser();

        this.saveUserInfo();

        if (this.isUserValid)
            this.loginProvider.auth(this.user)
                .subscribe(
                    this.registerSuccess.bind(this),
                    this.registerError.bind(this),
                    this.registerComplete.bind(this)
                );
    }

    private registerSuccess(result: any) {
        this.logger.success(result);
        this.userService.saveUser(result.objects[0]);
        this.route();
    }

    private registerError(err) {
        this.util.error(err.error.errorMessages[0]);
        this.logger.error(err.error.errorMessages[0]);
    }

    private registerComplete() {
        this.logger.info("Complete");
    }

    route() {
        this.nav.push(TodoListPage);
    }


    validateUser() {
        if (this.util.isEmpty(this.user.email)) {
            this.util.error("Email field is empty");
            this.isUserValid = false;
            return;
        }

        if (!this.util.mailRegex().test(this.user.email)) {
            this.util.error("Email format is wrong");
            this.isUserValid = false;
            return;
        }

        if (this.util.isEmpty(this.user.password)) {
            this.util.error("Password field is empty");
            this.isUserValid = false;
            return;
        }

        if (!this.util.passwordRegex().test(this.user.password)) {
            this.util.error("Password format is wrong. At least 6 char. 1-Capital, 1-lowercase, 1-number should be included.");
            this.isUserValid = false;
            return;
        }

        this.isUserValid = true;
    }


    async downloadApplicationDatas() {


    }

    hideShowPassword() {
        this.passwordType = this.showPassword ? "text" : "password";

    }

    checkLoginInfo() {
        if (this.util.isEmpty(this.user.email)) {
            this.util.warn("Email cannot be NULL");
            return false;
        }

        if (this.util.isEmpty(this.user.password)) {
            this.util.warn("Password cannot be NULL!!");
            return false;
        }
    }


    saveUserInfo() {
        localStorage.setItem(Constants.USER_EMAIL, this.user.email);
        localStorage.setItem(Constants.USER_PASSWORD, this.user.password);
    }

    startLoader() {
        this.util.loaderStart(false);
    }

    endLoader() {
        this.util.loaderEnd();
    }

    goRegisterPage() {
        this.nav.push(RegisterPage);
    }
}



