import {Pipe, PipeTransform} from '@angular/core';

/**
 * @author mali.sahin
 * @since 13.09.2018
 */
@Pipe({
  name: 'islemTipiPipe',
})
export class IslemTipiPipe implements PipeTransform {
  /**
   * Islem tipine göre açıklama değerini döner
   */
  transform(value: string, ...args) {
    let explanation = value;

    switch (value) {
      case "KM":
        explanation = "Yol";
        break;

      case "MLZ":
        explanation = "Malzeme";
        break;

      case "ISC":
        explanation = "İşçilik";
        break;

      case "DGR":
        explanation = "Diğer";
        break;

      default:
        break;

    }
    return explanation;
  }
}



