import {Component} from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams} from 'ionic-angular';
import {TodoList} from "../../entities/TodoList";
import {TodoItem} from "../../entities/TodoItem";
import {TodoItemEndpoint} from "../../providers/todo-item-endpoint/todo-item-endpoint";
import {UtilProvider} from "../../providers/util/util";
import {Observable} from "rxjs/Observable";

/**
 * @author mali.sahin
 * @since 15.10.2018
 */

@IonicPage()
@Component({
    selector: 'page-todo-item',
    templateUrl: 'todo-item.html',
})
export class TodoItemPage {

    todoList: TodoList;
    list: TodoItem[] = [];

    createStatus: boolean = false;
    todoItem: TodoItem;

    constructor(public navCtrl: NavController,
                private util: UtilProvider,
                public navParams: NavParams,
                private modalController: ModalController,
                private todoItemEndpoint: TodoItemEndpoint) {

        this.createStatus = false;
        this.todoList = this.navParams.get("todoList");
        this.todoItem = new TodoItem();
        this.todoItem.listId = this.todoList.id;


        this.getList();
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad TodoItemPage');
    }

    getList() {
        let call = this.todoItemEndpoint.list(this.todoList.id);

        call.subscribe(
            (res) => {
                this.list = this.util.processResult(res);
                console.log(res);
            },
            (err) => {
                this.util.processResult(err.error);
                console.error(err);
            },
            () => {
                console.info("Completed")
            }
        );
    }

    delete(todoItem: TodoItem) {
        let call = this.todoItemEndpoint.delete(todoItem.id);
        this.onComplete(call);
    }

    onComplete(call: Observable<any>) {
        call.subscribe(
            () => {
                this.getList();
            },
            (err) => {
                this.util.processResult(err.error);
                console.error(err);
            },
            () => {
                console.info("Completed")
            }
        );
    }

    changeCreateStatus() {
        this.createStatus = !this.createStatus;
    }

    createNewOne() {
        this.changeCreateStatus();
    }

    save() {
        if (this.util.isEmpty(this.todoItem.explanation)) {
            this.util.error("Explanation cannot be empty");
            return;
        }

        let call = this.todoItemEndpoint.save(this.todoItem);
        this.onComplete(call);
        this.changeCreateStatus();
    }
}



