import {Injectable} from "@angular/core";

@Injectable()
export class LoggerProvider {

    isLogEnabled: boolean = true;

    constructor() {
    }


    info(res) {
        if (this.isLogEnabled)
            console.log("%c " + res, "color: #0096ff");
    }

    success(message?: any, ...optionalParams: any[]) {
        if (this.isLogEnabled) {
            optionalParams.push("color: #00ff4b");
            console.log("%c " + message, ...optionalParams);
        }
    }

    log(message?: any, ...optionalParams: any[]) {
        if (this.isLogEnabled)
            console.log(message, optionalParams);
    }

    dir(message?: any, ...optionalParams: any[]) {
        if (this.isLogEnabled)
            console.dir(message, optionalParams);
    }

    error(message?: any, ...optionalParams: any[]) {
        if (this.isLogEnabled)
            console.error(message, optionalParams);

    }

    warn(message?: any, ...optionalParams: any[]) {
        if (this.isLogEnabled)
            console.warn(message, optionalParams);
    }

    table(...res) {
        if (this.isLogEnabled)
            console.table(res);
    }
}



